    background-image: url('');
}
.container{
    width: 95%;
    margin: auto;
    padding-top: 130px;
    border: 3px solid #c7ff84;

}
.heading{
    width: 55%;
    text-align: left;
    padding-left: 100px;
    position: relative;
    z-index: 99;
}
.heading::before{
    content: '';
    position: absolute;
    width: 120px;
    height: 1000px;
    left: 40%;
    top: -180%;
    z-index: -90;
    background-image: linear-gradient(to bottom right,#b2e673 40%, rgb(95, 233, 233) 100%,#d3f3ab 40%); 
     box-shadow : 0 0 50px #adf5f1, 0 0 50px #bdfcbd, 0 0 100px #f9fafa; 
    animation: animate 5s linear infinite;
}
.heading:hover{
    background-color: aqua;
} 
@keyframes animate{
    0%
    {
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}

.heading1{
    color: #c7ff84; 
    font-family: 'Roboto Slab', serif;
    font-size: 50px;
    font-weight: bold;
    
}
.heading2{
   font-size: 18px;
   font-weight: bolder;
}
.heading3{
    font-size: 40px;
    line-height: 45px;
    font-weight: bold;
    font-family: 'Archivo Black', sans-serif;
    color: #c7ff84; 
}

.h3{
    animation: shine 1s ease-in-out infinite alternate;
}
@keyframes shine {
    from {
      text-shadow: 0 0 1px #bee7d8, 0 0 5px #99f3e7, 0 0 5px #020b14;
    }to{
        text-shadow: 0 0 5px black;
    }
}
.heading4{
    font-size: 45px;
    line-height: 45px;
    font-weight: bold;
    font-family: 'Archivo Black', sans-serif;
    color: #c7ff84; 
    animation: glow 1.3s ease-in-out infinite alternate;
}
@keyframes glow {
    from {
      text-shadow: 0 0 15px #bee7d8, 0 0 10px #99f3b4, 0 0 5px #5bec5b, 0 0 3px #076e0c, 0 0 5px #020b14;
    }to{
        text-shadow: 0 0 5px black;
    }
}
.heading5{
    animation: flicker 1.2s infinite alternate;
    color:#c7ff84 ;
}

